import React, { useContext } from 'react';
import { FaLayerGroup } from 'react-icons/fa';
import List from '../_primitives/List';
import BaseComponent from '../_common/BaseComponent';
import LayerListItem from './LayerListItem';
import Context from '../../DefaultContext';
import mapExists from '../../util/mapExists';
import GroupLayer from './GroupLayer';
import { Text } from '@theme-ui/components';

const LayerList = ({
  layers,
  groupLayers,
  legend = false,
  showActions,
  checkboxStyle,
  baseType,
  buttonOptions,
  sx,
  ...rest
}) => {
  const config = useContext(Context);
  const { map } = config;

  if (!mapExists(map)) return null;
const Description = () => {
  return(
  <Text>
    Toggle layers:
    </Text>
  )
}
  const listItems = layers.map((item) => {
    return (
      <LayerListItem
        key={item.layerName}
        layerInfo={item}
        map={map}
        legend={legend}
        itemActions={item.actions}
        showActions={showActions}
        checkboxStyle={checkboxStyle}
        baseType={baseType}
      />
    );
  });

  let groupItems;
  if (groupLayers && groupLayers.length > 0) {
    groupItems = groupLayers.map((group) => {
      return <GroupLayer group={group} map={map} legend={legend} />;
    });
  }

  return (
    <BaseComponent
      baseType={baseType}
      buttonOptions={{
        icon: <FaLayerGroup />,
        title: 'Layer List',
        testId: 'cl-layerlist-btn',
        ...buttonOptions
      }}
      sx={sx}
      {...rest}
    >
      <Description/>
      <List>{listItems}</List>
      <List> {groupItems}</List>
    </BaseComponent>
  );
};

export default LayerList;
